import kleur from "kleur"
import Logger from "../../helpers/Logger.js"

const createNext = (req, res, locals) => {
    return (path) => {
        if(locals.handled === true || locals.aborted === true) return
        if(!path || typeof path !== 'string') return Logger(`${kleur.blue().bold('Next')} function argument must be a string`, 'controller')
        locals.next = path
        locals.aborted = true
    }
}

export default createNext